  font-size: 14px;
  color: #283645; }

.gallery__comment {
  margin: 0;
  padding-left: 16px;
  line-height: 24px; }

.gallery__caption {
  text-align: center;
  padding: 21px 20px 28px;
  line-height: 30px; }

.gallery__item {
  background-color: #f2f2f2;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 21px; }

.gallery__wrapper {
  padding-top: 17px; }

.gallery__photo {
  width: 100%;
  height: auto; }

.gallery__item:nth-child(5) .gallery__likes {
  padding-top: 10px; }

@media (min-width: 660px) {
  .gallery__list {
    display: grid;
    grid-template-columns: 1fr 1fr; }
  .gallery__item:nth-child(1n) {
    margin-right: 10px; }
  .gallery__item:nth-child(2n) {
    margin-left: 10px;
    margin-right: 20px; }
  .gallery__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
  .gallery__autor {
    padding-left: 30px;
    padding-top: 15px; }
  .gallery__comment {
    flex-grow: 1;
    padding-left: 30px;
    padding-right: 15px;
    min-height: 50px; }
  .gallery__likes {
    padding-left: 30px;
    padding-bottom: 20px; }
  .gallery__likes::before {
    left: 86%; }
  .gallery__caption {
    padding-top: 24px;
    padding-bottom: 49px; } }

@media (min-width: 960px) {
  .gallery__list {
    grid-template-columns: 1fr 1fr 1fr;
    max-width: 900px;
    margin: auto;
    grid-gap: 20px; }
  .gallery__item:nth-child(1n),
  .gallery__item:nth-child(2n) {
    margin: 0; } }

@media (min-width: 960px) {
  .gallery__list {
    grid-template-columns: 1fr 1fr 1fr;
    max-width: 940px;
    margin: auto;
    grid-row-gap: 55px; }
  .gallery__autor {
    padding-left: 27px; }
  .gallery__comment {
    padding-left: 27px; }
  .gallery__likes {
    padding-left: 27px; }
  .gallery__caption {
    padding-top: 39px; } }

.editor__wrapper {
  background-color: #f2f2f2;
  padding: 20px;
  padding-bottom: 30px;
  margin-bottom: 40px;
  position: relative; }

.editor__button {
  font-weight: 700;
  font-size: 18px;
  color: #d22856;
  font-family: inherit;
  border-radius: 30px;
  min-width: 280px;
  min-height: 55px;
  border: 3px solid #d22856;
  cursor: pointer;
  margin-bottom: 12px;
  outline: none; }

.editor__button:hover {
  background-color: #d22856;
  color: #ffffff; }

.editor__button:active {
  background-color: #d22856;
  color: rgba(255, 255, 255, 0.3); }

.editor__button--reset {
  font-weight: 700;
  font-size: 18px;
  color: #283645;
  font-family: inherit;
  border-radius: 30px;
  min-width: 280px;
  min-height: 55px;
  border: 3px solid #283645;
  opacity: 0.1;
  cursor: pointer; }

.editor__button--reset:hover {
  background: transparent;
  color: #283645;
  opacity: 0.3; }

.editor__button--reset:active {
  background: #283645;
  color: rgba(255, 255, 255, 0.3);
  opacity: 0.3; }

.editor__photo {
  width: 100%;
  height: auto; }

.editor__icons {
  display: flex;
  justify-content: space-between;
  min-width: 200px;
  padding-top: 36px;
  padding-bottom: 37px;
  padding-left: 20px;
  padding-right: 24px; }

.editor__crop {
  width: 44px;
  height: 44px;
  background-image: url("../img/crop.svg");
  background-repeat: no-repeat;
  cursor: pointer; }

.editor__fill {
  width: 48px;
  height: 43px;
  background-image: url("../img/fill.svg");
  background-repeat: no-repeat;
  cursor: pointer; }

.editor__contrast {
  width: 33px;
  height: 33px;
  background-image: url("../img/contrast.svg");
  background-repeat: no-repeat;
  margin-top: 6px;
  cursor: pointer; }

.editor__bar-crop,
.editor__bar-fill,
.editor__bar-contrast {
  width: 240px;
  height: 2px;
  background: #283645;
  margin-bottom: 70px; }

.editor__bar-fill,
.editor__bar-contrast {
  display: none; }

.editor__config {
  display: flex;
  flex-direction: column;
  align-items: center; }

.editor__wrapper::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background: #283645;
  opacity: 0.1;
  left: 0;
  bottom: 200px; }

.editor__crop-toggle,
.editor__fill-toggle,
.editor__contrast-toggle {
  position: relative;
  cursor: pointer; }

.editor__crop-toggle::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: #ffffff;
  border: 2px solid #d22856;
  border-radius: 50%;
  right: 30px;
  top: -6px; }

.editor__crop-toggle:hover::before {
  background-color: #d22856; }

.editor__button-wrapper {
  max-width: 280px;
  margin: auto; }

@media (max-width: 659px) {
  .editor__crop,
  .editor__fill,
  .editor__contrast {
    cursor: pointer; }
  .editor__crop:hover,
  .editor__fill:hover,
  .editor__contrast:hover {
    filter: brightness(-200%); }
  .editor__current:hover {
    filter: brightness(100%); } }

